import banner from "../components/banner.vue";
export default {
  components: {
    banner
  },
  data() {
    return {
      title: '',                         // 顶部标题
      swiperList: [],   // 轮播图
      timeList: [],                      // 时间
      selectIndex: 0,                    // 选中的时间
      goodList: [],                      // 商品列表
    }
  },
  onLoad(option) {
    console.log('分享文章详情页接受到的参数',option.origin)
    this.title = option.origin
    uni.setNavigationBarTitle({
      title: option.origin
    })
  },
  onShow() {
    // 获取秒杀或者限时购活动列表
    this.getSeckillCategorys()
  },
  watch: {
    selectIndex(val, oldval) {
      // 点击顶部时间动态获取秒杀商品
      this.getgoodsbycategoryid(this.timeList[val].category_id)
    }
  },
  methods: {
    // 获取秒杀活动列表
    getSeckillCategorys() {
      let url = this.$api.seckill_categorys
      if(this.title === '限时购') {
        url = this.$api.flashsale_categorys
      }
      this.$http({
        url: url,
        cb: (err, res) => {
          if(!err && res.code === 1) {
            this.timeList = res.data.list
            
            // 通过第一个活动ID获取秒杀商品
            this.getgoodsbycategoryid(this.timeList[0].category_id)
            
          } else if(res.code === 0 || res.code === -1 & res.msg) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '秒杀活动列表加载失败',
              icon: 'none'
            })
          }
        }
      })
    },
    // 通过秒杀活动ID获取秒杀商品列表
    getgoodsbycategoryid(id) {
      let that = this
      let url = that.$api.seckill_goodsbycategoryid
      if(that.title === '限时购') {
        url = that.$api.flashsale_goodsbycategoryid
      }
      that.$http({
        url: url,
        data: {
          category_id: id
        },
        cb: (err, res) => {
          if(!err && res.code === 1) {
            console.log(that.title, res.data.list)
            that.swiperList= res.data.list.banners
            that.goodList = res.data.list
          } else if(res.code === 0 || res.code === -1 && res.msg) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: that.title + '商品列表加载失败',
              icon: 'none'
            })
          }
        }
      })
    },
    goBuy(item) {
      console.log(item)
    },
    // 抢购提醒
    setRemind(item, index) {
      console.log(item, index)
      let that = this
      let url= that.$api.seckill_remind
      if(that.title === '限时购') {
        url= that.$api.flashsale_remind
        if(item.remind === 'yes') {
          url= that.$api.flashsale_cancelremind
        }  
      } else if(that.title === '秒杀购') {
        // url = 
      }
      
      that.$http({
        url: url,
        data: {
          good_id: item.goods_id,
          activity_category_id: item.activity_category_id,
          formId: 'app'
        },
        cb: (err, res) => {
          if(!err && res.code === 1) {
            console.log(res.data)
            
          } else if(res.code === 0 || res.code === -1 && res.msg) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '设置失败',
              icon: 'none'
            })
          }
        }
      })
    },
    // 选择活动
    selectTime(item, index) {
      this.selectIndex = index
    },
    // 去购买或者进入详情页
    goDetail(item) {
      console.log('点击了商品', item)
      // uni.navigateTo({
      //   url: '../components/goodDetail?info=' + info + '&panic=true'
      // })
    }
  },
}
